
.contactContainer {
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.createTogether {
  color: white;
  font-size: 4em
}

.contactBtn {
  height: 8%;
  width: 12%;
  border-radius: 20px;
  font-size: 1.5em;
}

@media (max-width: 1024px) {
  .contactContainer {
    height: 60vh;
  }
  .createTogether{
    font-size: 2em
  }
  .contactBtn{
    height: 10%;
    width: 20%
  }
}

@media (max-width: 800px) {
  .contactBtn{
    height: 10%;
    width: 35%
  }
}


