.what-we-do {
  width: 100vw;
  height: 100vh;
  min-width: 1025px;
  min-height: 56vw;

  position: absolute;
  top: 0%;
  left: 0%;

  display: flex;
  align-items: center;
  justify-content: space-evenly;

  color: white;
}

.what-we-do__left {
  width: 42vw;
  font-family: "Prompt", sans-serif;
  font-size: 4.2vw;

  position: relative;
  left: 5.8%;
}

.what-we-do__right {
  width: 42vw;
  font-family: "Lato", sans-serif;
  opacity: 0.7;
  font-size: 1.8vw;

  position: relative;
  right: 5.2%;
}

/* 
code below is the css for both services and departments
*/

.departments {
  width: 100vw;
  height: 100vh;
  min-height: 56vw;
  min-width: 1025px;
  position: absolute;
  top: 56vw;
  left: 0vw;
  color: white;

  display: grid;
  grid-template-areas:
    "service-heading service-heading"
    "service0 servicedesc"
    "service1 servicedesc"
    "service2 servicedesc"
    "footer footer";

  grid-template-columns: 50% 50%;
  grid-template-rows: 25% 15% 15% 15% 30%;
}

.services {
  width: 100vw;
  min-width: 1025px;
  height: 100vh;
  min-height: 56vw;
  position: absolute;
  top: 0vw;
  left: 0vw;
  color: white;

  display: grid;
  grid-template-areas:
    "service-heading service-heading"
    "service0 servicedesc"
    "service1 servicedesc"
    "service2 servicedesc"
    "footer footer";

  grid-template-columns: 50% 50%;
  grid-template-rows: 25% 15% 15% 15% 30%;
}

.services__left-heading {
  grid-area: service-heading;
  font-family: "Prompt";
  font-weight: 200;
  font-size: 5vw;

  text-align: left;
  position: relative;
  top: 20%;
  left: 10%;

  width: 50vw;
}

.services__left-text {
  font-family: "Prompt", sans-serif;
  font-weight: 200;
  font-size: 3vw;

  height: 15vh;
  width: 40%;

  position: absolute;
  left: 20%;

  margin-top: 1vh;
  margin-bottom: 1vh;

  opacity: 0.6;
}

.services__left-text--enter-animation {
  font-family: "Prompt", sans-serif;
  font-weight: 200;
  font-size: 3vw;

  height: 15vh;
  width: 40%;

  position: absolute;
  left: 20%;

  margin-top: 1vh;
  margin-bottom: 1vh;
}

.services__left-text--exit-animation {
  font-family: "Prompt", sans-serif;
  font-weight: 200;
  font-size: 3vw;

  height: 15vh;
  width: 40%;

  position: absolute;
  left: 20%;

  margin-top: 1vh;
  margin-bottom: 1vh;
  opacity: 0.6;
}

.text-0 {
  grid-area: service0;
}

.text-1 {
  grid-area: service1;
}

.text-2 {
  grid-area: service2;
}

.service__left-text {
  width: 40vw;

  position: absolute;
  top: -1vw;
  left: 5vw;
}

/*
css below for services and department animations
*/

.services__left-text span {
  font-size: medium;

  position: absolute;
  top: 4vw;
}

.services__left-text--enter-animation span {
  font-size: medium;

  position: absolute;
  top: 4vw;
  left: -8.5vw;

  animation: moveNumber 0.5s linear 1;
}

.services__left-text--exit-animation span {
  font-size: medium;

  position: absolute;
  top: 4vw;

  animation: moveNumberReverse 0.5s linear 1;
}

.services__left-text hr {
  width: 7%;
  align-self: center;
  opacity: 0;

  position: absolute;
  top: 3vw;
  left: 2vw;
}

.services__left-text--enter-animation hr {
  width: 45%;
  align-self: center;
  position: absolute;
  opacity: 1;

  position: absolute;
  top: 3vw;
  left: -6vw;

  animation: linear 0.5s 1 increaseLine;
}

.services__left-text--exit-animation hr {
  width: 7%;
  align-self: center;
  top: 3vw;
  left: 2vw;
  opacity: 0;

  animation: linear 0.5s 1 decreaseLine;
}

/*
css for right side of the what we do page 
*/

.departments-text {
  opacity: 0.8;
  font-family: "Lato", sans-serif;
  font-size: 2vw;
  position: relative;
  left: 10%;
  grid-area: servicedesc;
}

.service-pos-1 {
  top: 10%;
}

.service-pos-2 {
  top: 45%;
}

.services-text {
  opacity: 0.8;
  font-family: "Lato", sans-serif;
  font-size: 1.6vw;
  position: relative;
  left: 10%;
  grid-area: servicedesc;
}

.service-text-animation {
  width: 40vw;
  text-align: left;
  position: relative;
  top: 3vh;

  animation: showText 1s 1 linear;
}

.services-text ul {
  display: grid;
  grid-template-columns: 50% 50%;
  grid-template-rows: 30% 30% 30%;
  grid-template-areas:
    "list-left1 list-right1"
    "list-left2 list-right2 "
    "list-left3 list-right3 ";
  width: 50vw;
  position: relative;
  top: 3vh;
  font-family: "Lato", sans-serif;
  opacity: 0.8;
  font-size: 1.6vw;
  animation: showText 1s 1 linear;
}

.services-text ul > li:nth-child(1) {
  position: relative;
  grid-area: list-left1;
}

.services-text ul > li:nth-child(2) {
  position: relative;
  grid-area: list-right1;
}
.services-text ul > li:nth-child(3) {
  position: relative;
  grid-area: list-left2;
}

.services-text ul > li:nth-child(4) {
  position: relative;
  grid-area: list-right2;
}
.services-text ul > li:nth-child(5) {
  position: relative;
  grid-area: list-left3;
}

.services-text ul > li:nth-child(6) {
  position: relative;
  grid-area: list-right3;
}

@keyframes showText {
  from {
    opacity: 0;
    position: relative;
    top: 8vh;
  }

  to {
    opacity: 0.8;
    position: relative;
    top: 3vh;
  }
}

@keyframes increaseLine {
  from {
    opacity: 0;
    position: absolute;
    top: 3vw;
    left: 2vw;
    width: 7%;
  }

  to {
    opacity: 1;
    position: absolute;
    top: 3vw;
    left: -6vw;
    width: 45%;
  }
}

@keyframes decreaseLine {
  from {
    opacity: 1;
    position: absolute;
    top: 3vw;
    left: -6vw;
    width: 45%;
  }

  to {
    opacity: 0;
    position: absolute;
    top: 3vw;
    left: 2vw;
    width: 7%;
  }
}

@keyframes moveNumber {
  from {
    position: absolute;
    top: 4vw;
    left: 0vw;
  }

  to {
    position: absolute;
    top: 4vw;
    left: -8.5vw;
  }
}

@keyframes moveNumberReverse {
  from {
    position: absolute;
    top: 4vw;
    left: -8.5vw;
  }

  to {
    position: absolute;
    top: 4vw;
    left: 0vw;
  }
}

/*
css for upto 1024 width
*/

.what-we-do__heading--1024 {
  width: 90vw;
  margin-left: 10vw;
  font-family: "Prompt", sans-serif;
  font-size: 10vw;
}

.what-we-do__desc--1024 {
  margin-left: 10vw;
  margin-right: 10vw;
  opacity: 0.7;
  margin-bottom: 10vh;
  font-family: "Lato", sans-serif;
  font-size: 6vw;
}

.services-cont--1024 {
  display: flex;
  align-items: center;
  margin-left: 10vw;
  margin-right: 1vw;
}

.services-cont--1024 p {
  font-size: 7vw;
  font-weight: 100;
  font-family: "Prompt", sans-serif;
}

.services-cont--1024 span {
  font-size: 4.5vw;
  margin-right: 5vw;
}

.services-cont__desc--1024 {
  height: 70vh;
}

.services-cont__desc--1024 p {
  width: 80vw;
  font-size: 5vw;
  opacity: 0.8;
  position: relative;
  left: 10vw;

  animation: moveTextInFromRight 0.2s 1 linear;
}

.services-cont__desc--1024 ul {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 80vw;
  font-size: 5vw;
  opacity: 0.8;
  animation: moveTextInFromRight 0.2s 1 linear;
}

.services-cont__desc--1024 ul > li {
  margin: 0vw 3vw 0vw 3vw;
}

/*
css animations for upto 1024 services
*/

@keyframes moveTextInFromRight {
  from {
    position: relative;
    left: 40vw;
  }

  to {
    position: relative;
    left: 15vw;
  }
}
