.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  min-width: 1025px;
  display: flex;
  justify-content: space-between;
  z-index: 11;
}

.header__logo {
  width: 6.15%;
  position: fixed;
  top: 1.9vw;
  left: 3%;
  cursor: pointer;
}

.header__menu {
  width: 2.6%;
  position: fixed;
  top: 4.5vw;
  right: 6.1%;
  cursor: pointer;
}

/*
css for mobile header
*/

.header-1024 {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;

  display: flex;
  justify-content: space-between;

  z-index: 11;
}

.header-1024__logo {
  width: 14%;
  position: relative;
  top: 2.2vh;
  left: 7.7%;
}

.header-1024__menu {
  width: 8%;
  height: 2.5vh;
  position: relative;
  top: 4.4vh;
  right: 8%;
}
