.landing-page {
  position: absolute;
  width: 100vw;
  min-width: 1025px;
  height: 100vh;
  min-height: 665px;
  overflow-x: hidden;
}

@media (min-width: 1025px) {
  .landing-page {
    overflow: hidden;
  }

  .home-page__nav-cont h1 {
    transform: translateX(15rem);
    opacity: 0;
    transition: transform 550ms ease-out;
  }

  .home-page__nav-cont:nth-child(3) h1 {
    transform: translateX(-15rem);
    opacity: 0;
    transition: transform 550ms ease-out;
  }

  .animated-text h1 {
    transform: translateX(0) !important;
    opacity: 1 !important;
  }

  .home-page__nav-cont p {
    transform: translateX(15rem);
    opacity: 0;
    transition: transform 550ms ease-out;
  }

  .home-page__nav-cont:nth-child(3) p {
    transform: translateX(-15rem);
    opacity: 0;
    transition: transform 550ms ease-out;
  }

  .animated-text p {
    transform: translateX(0) !important;
    opacity: 1 !important;
  }
}

.land-home__nav {
  position: absolute;
  left: 92%;
  top: 10%;
  width: 2.6%;
  z-index: 10;
  cursor: pointer;
}

.landing-page__cont {
  position: absolute;
  top: 0%;
  left: 0%;
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-areas:
    "left right"
    "footer footer";
  grid-template-columns: 50% 50%;
  grid-template-rows: 80% 20%;
}

.landing-page__logo {
  grid-area: left;
  width: 44%;

  position: absolute;
  top: 60%;
  left: 50%;
  transform: translateX(-50%) translateY(-45%);
}

.landing-page__text {
  color: white;
  font-family: "Prompt", sans-serif;
  font-size: 300%;
  grid-area: right;
  width: 80%;

  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  animation: fadeInLeft 0.4s 1 linear;
}

.landing-page__video-cont {
  position: absolute;
  width: 100%;
  min-width: 1190px;
  height: 100%;
  z-index: -1;
  overflow: hidden;
}

.landing-page__video-cont video {
  width: 100%;
  height: 100%;
}

.landing-page__scroll-btn {
  grid-area: footer;
}

.landing-page__scroll-btn__arrow {
  border: 30px;
  position: relative;
  top: 50%;
  left: 50%;

  animation: arrowAnimate infinite 1s ease-in-out;
}
/*
css for home page
*/

.home-page--unscrollable {
  width: 100vw;
  min-width: 1025px;
  height: 100vh;
  min-height: 608px;
  overflow: hidden;
  position: absolute;
}

.home-page {
  width: 100vw;
  min-width: 1025px;
}

.home-page__kinetic-typo {
  position: relative;
  width: 100%;
  height: 100vh;
  min-height: 608px;
  display: grid;
  grid-template-areas:
    "left right"
    "footer footer";
  grid-template-columns: 50% 50%;
  grid-template-rows: 80% 20%;
}

.home-page__logo {
  position: fixed;
  left: 3%;
  top: 4%;
  width: 6.2%;
  z-index: 15;
  cursor: pointer;
  animation: toSmallLogo 0.7s 1 linear;
}

.home-page__kinetic-typo__vid-cont {
  position: absolute;
  width: 100vw;
  min-width: 1025px;
  height: 100vh;
  min-height: 608px;
  overflow: hidden;
}

.home-page__kinetic-typo__vid {
  position: absolute;
  top: -10vw;
  width: 100vw;
  min-width: 1300px;
  z-index: -1;

  animation: enterHomePage 1s linear 1;
}

.home-page__kinetic-typo__vid--phone {
  display: none;
}

.home-page__nav-cont {
  position: relative;
  width: 100vw;
  min-width: 1025px;
  height: 100vh;
  min-height: 608px;
  display: flex;
  align-items: center;
  justify-content: space-around;

  color: white;
}

.home-page__nav-cont div {
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.home-page__nav__heading {
  font-family: "Prompt";
  font-weight: 200;
  font-size: 280%;
}

.home-page__nav__text {
  font-family: "Lato";
  font-weight: 200;
  font-size: 150%;
  width: 60%;
  margin-top: 0px;
  opacity: 0.7;
}

.home-page__whatwedo {
  width: 25%;
  position: relative;
  left: 10%;
}

.home-page__team {
  width: 40%;
  margin-right: 5%;
}

.home-page__work {
  width: 40%;
}

.home-page__nav__arrow {
  width: 150px;
  position: relative;
  left: 50%;
  transform: translateX(0px);
  transition: transform 250ms ease-in;
}

.home-page__nav__arrow:hover {
  transform: translateX(20px);
  transition: transform 250ms ease-in;
}
/*
code for css animations
*/

@keyframes arrowAnimate {
  0% {
    top: 0px;
  }
  30% {
    top: 10px;
  }
  70% {
    top: 10px;
  }
  100% {
    top: 0px;
  }
}

@keyframes toSmallLogo {
  from {
    width: 22%;

    position: fixed;
    top: 50%;
    left: 30%;
    transform: translateX(-50%) translateY(-50%);
  }
  to {
    position: fixed;
    left: 4%;
    top: 3%;
    width: 6.2%;
  }
}

@keyframes fadeInLeft {
  from {
    display: flex;
    grid-area: right;
    position: absolute;
    top: 50%;
    left: 40%;
    transform: translateX(-50%) translateY(-50%);
    opacity: 0.1;
  }

  to {
    display: flex;
    grid-area: right;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);

    opacity: 1;
  }
}

@keyframes enterHomePage {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes arrowLeft {
  from {
    left: 0%;
  }

  to {
    left: 5%;
  }
}

/*
code for the mobile landing page
*/

@media only screen and (max-width: 1024px) {
  .landing-page {
    position: absolute;
    width: 100vw;
    height: 100vh;
    min-width: 100vw;
    min-height: 100vh;
    overflow-x: hidden;
  }

  .land-home__nav {
    position: absolute;
    left: 84%;
    top: 4.4%;
    width: 7.7%;
    z-index: 10;
    cursor: pointer;
  }

  .landing-page__cont {
    position: absolute;
    height: 100vh;
    width: 100vw;
    display: block;
  }

  .landing-page__logo {
    position: absolute;
    width: 55%;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
  }

  .landing-page__text {
    position: absolute;
    top: 70%;
    color: white;
    font-family: "Prompt", sans-serif;
    font-size: 200%;
    width: 100vw;
    text-align: center;

    animation: none;
  }

  .landing-page__video-cont {
    position: absolute;
    width: 100vw;
    height: 100vh;
    min-height: 100vh;
    min-width: 100vw;
    overflow: hidden;
  }

  .landing-page__video-cont video {
    height: 100vh;
    width: auto;
    position: absolute;
    left: -150%;
  }

  .landing-page__scroll-btn {
    position: absolute;
    top: 90%;
    left: 50%;
  }

  .landing-page__scroll-btn__arrow {
    animation: arrowAnimate infinite 1s ease-in-out;
  }

  /*
  css for the homepage
  */

  .home-page--unscrollable {
    width: 100vw;
    min-width: 100vw;
    height: 100vh;
    min-height: 100vh;
    overflow: hidden;
    position: absolute;
  }

  .home-page {
    width: 100vw;
    min-width: 100vw;
  }

  .home-page__kinetic-typo {
    position: relative;
    width: 100%;
    height: 100vh;
    min-height: 100vh;
    display: block;
  }

  .home-page__kinetic-typo img {
    position: absolute;
    top: 2.2%;
    left: 7.7%;
