  }

  .home-page__kinetic-typo__vid-cont {
    position: absolute;
    width: 100vw;
    min-width: 100vw;
    height: 100vh;
    overflow: hidden;
  }

  .home-page__kinetic-typo__vid {
    display: none;
  }

  .home-page__kinetic-typo__vid--phone {
    display: block;
    position: absolute;
    height: 100vh;

    animation: enterHomePage 1s 1 linear;
  }

  .home-page__nav-cont {
    position: relative;
    width: 100vw;
    min-width: 100vw;
    height: 100vh;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;

    color: white;
  }

  .home-page__nav-cont div {
    display: block;
    width: 100vw;
    min-width: 100vw;

    position: absolute;
    top: 0%;
    left: 0%;
  }

  .home-page__nav__heading {
    font-family: "Prompt";
    font-size: 5vh;
    text-align: center;

    width: 100vw;

    position: absolute;
    top: 10vh;
  }

  .home-page__nav__text {
    font-family: "Lato";
    font-size: 3vh;
    width: 80%;
    border-left: black solid;
    border-right: black solid;
    border-width: 10vw;
    text-align: center;
    position: absolute;
    top: 65vh;
    left: 50%;
    transform: translateX(-50%);

    opacity: 0.6;
    background-color: black;
  }

  .home-page__whatwedo {
    width: 89.3%;
    position: absolute;
    top: 21%;
  }

  .home-page__team {
    width: 90%;
    position: absolute;
    left: 5%;
    z-index: -1;
  }

  .home-page__work {
    width: 130%;
  }

  .home-page__nav__arrow {
    width: 30%;
    position: absolute;
    top: 91vh;
    left: 27.8vw;

    transform: translateX(60%);
  }
}
