.navigation-page {
  position: absolute;
  height: 100vh;
  width: 100vw;
  background-color: #181818;
  overflow: hidden;
  animation: showNavPage 1s linear 1;
}

.navigation-page__close-btn {
  position: absolute;
  top: 10%;
  left: 90%;
  background-color: transparent;
  border: none;
}

.navigation-page__links {
  position: absolute;
  top: 50%;
  left: 50%;

  transform: translateX(-50%) translateY(-50%);
}

.navigation-page__links h1 {
  font-family: "Prompt", sans-serif;
  font-size: 3vw;
  color: white;
  text-align: center;
}

.navigation-page__back-img {
  position: absolute;
  top: 50%;
  left: 50%;

  transform: translateX(-50%) translateY(-45%);

  opacity: 0.05;
  z-index: 0;
  width: 30%;
}

.navigation-page__close-img {
  width: 30px;
  height: 30px;
}

@keyframes showNavPage {
  from {
    opacity: 0;
  }

  to {
    background-color: #181818;
    opacity: 1;
  }
}

/*
css code for the mobile navigation version
*/

@media only screen and (max-width: 1024px) {
  .navigation-page__links {
    width: 80%;

    position: absolute;
    top: 50%;
    left: 50%;

    transform: translateX(-50%) translateY(-50%);
  }
  .navigation-page__links h1 {
    font-family: "Prompt", sans-serif;
    font-size: 7.5vw;
    color: white;
    text-align: center;
  }

  .navigation-page__back-img {
    position: absolute;
    top: 50%;
    left: 50%;

    transform: translateX(-50%) translateY(-45%);

    opacity: 0.05;
    z-index: 0;
    width: 70%;
  }
}
