.team__landing-cont {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  min-width: 1025px;
  height: 100vh;
  min-height: 605px;
}

.team__heading {
  width: 43%;
  color: white;
  font-family: "Prompt", sans-serif;
  font-size: 250%;
}

.team__landing-svg {
  width: 43%;
}

.team-container {
  width: 100vw;
  color: white;
}

.members-box {
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  justify-content: center;
}

.member-box {
  width: 329px;
  margin-bottom: 103px;
  text-align: center;
  font-family: "Prompt", sans-serif;
  font-weight: 200;
}

.member-box img {
  width: 200px;
}

.team-header {
  width: 100%;
  text-align: center;
  font-family: "Prompt", sans-serif;
  font-size: 270%;
  font-weight: 200;
}

.member-box h2 {
  margin-top: 0px;
  text-align: center;
  margin-bottom: 0px;
}

.member-box h4 {
  text-align: center;
  margin: 0;
  opacity: 0.7;
}

.member-box h3 {
  text-align: center;
  margin: 0;
  opacity: 0.7;
}

@media only screen and (max-width: 1024px) {
  .team__landing-cont {
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center;
    flex-wrap: wrap;
    width: 100vw;
    min-width: 100vw;
    height: 100vh;
    min-height: 100vh;
  }

  .team__landing-svg {
    width: 80vw;
  }

  .team__heading {
    text-align: center;
    width: 80%;
    font-size: 150%;
    font-family: "Prompt", sans-serif;
  }
}
