.container {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.topPart {
  width: 100%;
  height: 10%;
  display: flex;
  justify-content: center;
}
.title {
  color: white;
  font-size: 4em;
  margin: 0 0;
}
.bottomPart {
  /* height: 120vh; */
  /* overflow-y: scroll; */
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.bottomLeft {
  padding-top: 0%;
}
.bottomRight {
  padding-top: 5%;
}

.projectContainer {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.projectImg {
  padding-top: 1%;
  height: 40%;
  width: 80%;
}

.projectTitle {
  font-family: Prompt;
  font-size: 1.8em;
  height: 10%;

  transition: top 2s;
  color: white;
  display: block;
  margin-block-start: 0em;
  margin-block-end: 0em;
  padding: 2% 0;
}
.projectDesc {
  font-family: Lato;
  font-size: 1.5em;
  height: 10%;
  width: 65%;
  color: white;
  transition: top 2s;
  margin-block-start: 0em;
  margin-block-end: 0em;
  padding-bottom: 2%;
}

.projectImg:hover {
  opacity: 0.4;
}

.projectImg:hover ~ .projectTitle {
  position: relative;
  top: -11rem;
}
.projectImg:hover ~ .projectDesc {
  position: relative;
  top: -11rem;
}
@media (max-width: 1200px) {
  .projectTitle {
    font-size: 1.6em;
  }
  .projectDesc {
    font-size: 1.25em;
  }
}
@media (max-width: 800px) {
  .title {
    font-size: 3em;
    padding-bottom: 4%;
  }
  .bottomPartMob {
    display: flex;
    width: 100%;
    flex-direction: column;
  }
  .projectContainer {
    padding: 6% 0;
  }
  .projectContainer:nth-child(1) {
    padding-top: 15%;
  }
  .projectContainer:nth-child(1) {
    padding-top: 15%;
  }
  .projectImg {
    width: 70%;
    padding-bottom: 3%;
  }
  .projectTitle {
    font-size: 1.8em;
  }
  .projectDesc {
    width: 65%;
    font-size: 1.4em;
  }
  .projectImg:hover ~ .projectTitle {
    position: relative;
    top: -12rem;
  }
  .projectImg:active ~ .projectTitle {
    position: relative;
    top: -12rem;
  }
  .projectImg:hover ~ .projectDesc {
    position: relative;
    top: -12rem;
  }
  .projectImg:active ~ .projectDesc {
    position: relative;
    top: -12rem;
  }
}
@media (max-width: 530px) {
  .title {
    font-size: 2.2em;
  }
  .projectTitle {
    font-size: 1em;
  }
  .projectDesc {
    font-size: 0.9em;
  }
  .projectImg:hover ~ .projectTitle {
    position: relative;
    top: -8.5rem;
  }
  .projectImg:active ~ .projectTitle {
    position: relative;
    top: -8.5rem;
  }

  .projectImg:hover ~ .projectDesc {
    position: relative;
    top: -8.5rem;
  }
  .projectImg:active ~ .projectDesc {
    position: relative;
    top: -8.5rem;
  }
}
