.Container {
  width: 100vw;
  height: 20vh;
  /* position: fixed; */

  top: 0px;
}

.slogo {
  position: absolute;
  left: 3vw;
  top: 3.5vh;
}
.slogo img {
  width: 5vw;
  min-width: 8em;
}

.navButton img {
  position: absolute;
  right: 4vw;
  top: 7vh;
  width: 5em;
}

.title {
  color: white;
  font-family: Prompt;
  display: block;
  font-size: 2em;
  margin-block-start: 0;
  margin-block-end: 0;
  padding-top: 0;
  text-align: center;
}
.title h1 {
  font-size: 2em;
  margin-block-start: 0;
  margin-block-end: 0;
  padding-top: 0;
  padding-top: 4vh;
}

@media (max-width: 800px) {
  .Container{
    height: 16vh;
  }
  .slogo img{
    min-width: 6em;
  }
  .navButton img {
    padding-top: 0% ;
    padding-right: 7%;;
    width: 3em
  }
  .title {
    padding-top: 10%;
    font-size: 1em
  }
}

@media (max-width: 500px) {
  .title {
    padding-top: 25%
  }
}
